

//grid system of the layout
@mixin grid($classname) {
    @for $i from 1 through 12{
        #{$classname}#{$i}{
            flex:0 0 100% / 12 * $i;
        }
    }
}

//media query
@mixin  mQ($max-width){
    @media screen and (max-width:$max-width){
        @content;
    }   
}

//clear fix

@mixin  clearFix {
    &::after{
        clear: both;
        content: "";
        display: table;
      }
}